<template>
  <div class="clearfix">
    <da-panel padding-content="trlb" :no-head="true">
      时间：
      <da-time-select v-model="alarmTime" :time-type.sync="timeType" :show-time="[3,5,8]" @change="changeAlarmTime"></da-time-select>
      <da-radio-select v-model="attentionAlarm" :maxNum="3" :option-lists="attentionAlarmList" label="name" value-key="id" @change="changeAttentionAlarm"></da-radio-select>
      <button type="button" class="el-button el-button--primary" @click="search">
        <span>查询</span>
      </button>
    </da-panel>

    <da-panel padding-content="tb" :border="false" :no-head="true">
      <da-equal-panel left-width="210px" right-width="240px" height="auto">
        <alarm-data slot="left" :dataLists="alarmLevelList" label="name" value-key="value" mode="vertical"></alarm-data>

        <alarm-data slot="right" :dataLists="alarmCountList" label="name" width="230px" value-key="value" mode="vertical"></alarm-data>

        <alarm-data class="panel-flex-1" :dataLists="exceptionCountList" label="name" width="20%" value-key="value" mode="horizontal"></alarm-data>
      </da-equal-panel>
    </da-panel>

    <da-panel title="告警信息分类分析" width="50%" height="auto" padding="rb" padding-content="lrtb" ref="panelCount">
      <da-pie-chart id="alarmAnalyze" title="告警信息分类分析" :data-lists="alarmAnalyzeData" label="name" valueKey="value"></da-pie-chart>
    </da-panel>
    <da-panel title="告警信息趋势分析" width="50%" height="auto" padding="b" padding-content="lrtb" ref="panelCount">
      <!-- alarmInfoTrendsData -->
      <da-line-chart type="line" :data-lists="alarmInfoTrendsData" x-axis-key="alarmTime" value-key="alarmNum" label="告警总数"></da-line-chart>
    </da-panel>
    <da-panel title="部门告警信息统计" width="50%" height="465px" overflow="auto" padding="rb" padding-content="lrtb" ref="panelCount">
      <!-- deptAlarmInfoTable  部门告警信息统计-->
      <da-table class="analysis-table" :colums="deptColumnData" :front-page="true" :table-list="deptAlarmInfoTable"></da-table>
    </da-panel>
    <da-panel title="设备告警信息统计" width="50%" height="465px" overflow="auto" padding="b" padding-content="lrtb" ref="panelCount">
      <!-- DevAlarmInfoTable  设备告警信息统计-->
      <da-table class="analysis-table" :colums="devColumnData" :front-page="true" :table-list="devAlarmInfoTable"></da-table>
    </da-panel>
    <da-panel title="告警确认情况统计" width="50%" height="250px" overflow="auto" padding="rb" padding-content="lrtb" ref="panelCount">
      <!--alarmConfirmTable  告警确认情况统计 -->
      <da-table class="analysis-table" :colums="confirmColumnData" :show-summary="true" :table-list="alarmConfirmTable"></da-table>
    </da-panel>
    <da-panel title="确认人确认数量统计" width="50%" height="250px" overflow="auto" padding="b" padding-content="lrtb" ref="panelCount">
      <!-- confirmCountTable 确认人确认数量统计 -->
      <da-table class="analysis-table" :colums="countColumnData" :table-list="confirmCountTable"></da-table>
    </da-panel>
  </div>
</template>

<script>
import { cycleTime, calcTime } from "../../../util/util";
import alarmData from "../template/alarmData";
export default {
  components: { alarmData },
  data() {
    return {
      timeType: 3,
      allAlarmData: [], ////所有告警详细数据
      alarmTime: "", //告警时间
      startTime: "", //查询开始的时间
      finishTime: "", //查询结束的时间
      alarmDepartment: "", //告警部门
      selectAttentionAlarm: "",
      alarmDepartmentList: [], //告警部门列表
      attentionAlarm: "", //关注的告警
      attentionAlarmList: [
        { id: 0, name: "所有的告警" },
        { id: 1, name: "我关注的告警" }
      ], //关注的告警列表
      alarmLevelList: [
        { name: "一类告警", value: 0, id: 0 },
        { name: "二类告警", value: 0, id: 1 },
        { name: "三类告警", value: 0, id: 2 },
        { name: "四类告警", value: 0, id: 3 }
      ], //告警级别列表
      alarmCountList: [
        { name: "告警总数", value: 0 },
        { name: "已确认告警", value: 0 },
        { name: "未确认告警", value: 0 },
        { name: "未确认一类告警", value: 0 }
      ], //告警统计列表
      exceptionCountList: [], //告警类型列表
      alarmAnalyzeData: [], //告警类型分析数据
      alarmInfoTrendsData: [], //告警信息趋势分析数据
      deptColumnData: [
        {
          label: "部门名称",
          prop: "orgId",
          displace: {}
        },
        {
          label: "告警总数",
          prop: "alarmCount"
        },
        {
          label: "一类",
          prop: "level_1"
        },
        {
          label: "二类",
          prop: "level_2"
        },
        {
          label: "三类",
          prop: "level_3"
        },
        {
          label: "四类",
          prop: "level_4"
        }
      ], //部门告警信息统计表头
      deptList: [], //部门列表
      deptAlarmInfoTable: [], //部门告警信息统计数据
      devList: [], //设备告警信息
      devColumnData: [
        {
          label: "设备名称",
          prop: "equipName"
        },
        {
          label: "所属部门",
          prop: "orgId",
          displace: {}
        },
        {
          label: "告警总数",
          prop: "alarmCount"
        },
        {
          label: "一类",
          prop: "level_1"
        },
        {
          label: "二类",
          prop: "level_2"
        },
        {
          label: "三类",
          prop: "level_3"
        },
        {
          label: "四类",
          prop: "level_4"
        }
      ], //设备告警信息统计表头
      devAlarmInfoTable: [], //设备告警信息统计数据
      confirmColumnData: [
        {
          label: "告警类型",
          prop: "alarmType"
        },
        {
          label: "告警数量",
          prop: "alarmCount"
        },

        {
          label: "一类告警",
          children: [
            {
              label: "已确认",
              prop: "level_1_1"
            },
            {
              label: "未确认",
              prop: "level_1_0"
            }
          ]
        },
        {
          label: "二类告警",
          children: [
            {
              label: "已确认",
              prop: "level_2_1"
            },
            {
              label: "未确认",
              prop: "level_2_0"
            }
          ]
        },
        {
          label: "已确认占比%",
          prop: "ratio"
        }
      ], //告警确认情况统计表头
      alarmConfirmTable: [], //告警确认情况统计数据
      countColumnData: [
        {
          label: "确认人",
          prop: "confirmUser"
        },
        {
          label: "所属部门",
          prop: "orgId"
        },
        {
          label: "确认总数",
          prop: "alarmCount"
        },
        {
          label: "占比",
          prop: "alarmCountProportion"
        },
        {
          label: "一类告警",
          prop: "level_3"
        },
        {
          label: "占比",
          prop: "leve3_proportion"
        },
        {
          label: "二类告警",
          prop: "level_4"
        },
        {
          label: "占比",
          prop: "level_4_proportion"
        }
      ], //确认人确认数量统计表头
      confirmCountTable: [] //确认人确认数量统计数据
    };
  },
  methods: {
    //初始化数据
    inintData() {
      this.finishTime = this.$moment(new Date(), "YYYY-MM-DD");
      this.beginTime = this.finishTime;
      this.getAttentionAlarm();
    },
    //加载页面
    loadData() {
      this.getExceptionCountList();
      this.getAlarmLevelCount();
      this.getConfirCount();
      this.getUnconfirOneCount();
      this.getAlarmInfoTrendsData();
      this.getDepartmentList();
      this.getDevAlarmInfoTable();
      this.getAlarmConfirmTable();
      this.getConfirmCountTable();
    },
    //切换时间
    changeAlarmTime() {
      if (this.timeType == 3) {
        this.beginTime = this.alarmTime;
        this.finishTime = this.alarmTime;
      } else if (this.timeType == 5) {
        let time = cycleTime(this.alarmTime, 5);
        this.beginTime = time[0];
        this.finishTime = time[1];
      } else {
        let time = cycleTime(this.alarmTime, 8);
        this.beginTime = time[0];
        this.finishTime = time[1];
      }
    },
    //获得我关注的告警
    getAttentionAlarm() {
      this.$ajax
        .get("rest/analysisData/alarmItem", {
          params: {
            title: "关注的告警", //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
            userIds: null, //用户ID，为空表示不作为查询条件，多个用逗号隔开，如"1,2,..."
            alarmGroups: null, //告警分组ID，为空表示不作为查询条件，多个用逗号隔开，如"1,2,..."
            receiveTypes: 1, //用户接收告警类型，1:弹窗；2：邮件；3：短信；4：微信，为空时表示不作为查询条件
            isAlarmGroup: null, //是否返回告警分组
            isReceiveType: true //是否返回用户接收告警类型
          }
        })
        .then(res => {
          if (res.data.status) {
            for (let i = 0; i < res.data.records.length; i++) {
              if (i < res.data.records.length) {
                this.selectAlarmCode += res.data.records[i].id + ",";
              } else {
                this.selectAlarmCode += res.data.records[i].id;
              }
            }
          }
        });
    },
    //切换我关注的告警
    changeAttentionAlarm() {
      if (this.attentionAlarm == 0) {
        this.selectAttentionAlarm = "";
      } else {
        this.selectAttentionAlarm = this.selectAlarmCode;
      }
    },
    //获得告警种类类型数据
    getExceptionCountList() {
      this.$ajax
        .get("rest/analysisData/alarmGroup", {
          params: {
            title: "查询告警种类" //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            this.$each(res.data.records, item => {
              data.push({
                name: item.name,
                value: 0
              });
            });
            this.exceptionCountList = data;
            this.getAlarmCategoryCount();
            this.getAlarmAnalyzeData();
          }
        });
    },
    //各类告警类型数据统计
    getAlarmCategoryCount() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警类型统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: this.selectAlarmCategory, // String,告警分组ID,告警类型
            alarmCodes: this.selectAlarmCode, // String,告警项编码
            isConfirm: null, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "001000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = res.data.records;
            if (data !== null) {
              for (let i = 0; i < this.exceptionCountList.length; i++) {
                for (let j = 0; j < data.length; j++) {
                  if (
                    this.exceptionCountList[i].name == data[j].alarmGroupName
                  ) {
                    this.exceptionCountList[i].value = data[j].alarmNum;
                  }
                }
              }
            }
          }
        });
    },
    //告警各级别数统计
    getAlarmLevelCount() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警级别统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: "", // String,告警级别
            alarmGroups: "", // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: null, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "010000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = res.data.records;
            if (data !== null) {
              //获得各等级报警数据统计
              for (let i = 0; i < data.length; i++) {
                for (let j = 0; j < this.alarmLevelList.length; j++) {
                  if (data[i].level == this.alarmLevelList[j].id + 1) {
                    this.alarmLevelList[j].value = data[i].alarmNum;
                  }
                }
              }
            }
          }
        });
    },
    //已确认告警统计
    getConfirCount() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "已确认告警统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: true, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "010000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            this.alarmCountList[1].value = 0;
            for (let i = 0; i < res.data.records.length; i++) {
              this.alarmCountList[1].value += res.data.records[i].alarmNum;
            }
          }
          this.getUnconfirCount();
          this.getConfirmCountTable();
        });
    },
    //未确认告警统计
    getUnconfirCount() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "未确认告警统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: false, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "010000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            this.alarmCountList[2].value = 0;
            for (let i = 0; i < res.data.records.length; i++) {
              this.alarmCountList[2].value += res.data.records[i].alarmNum;
            }
          }
          this.alarmCountList[0].value =
            this.alarmCountList[1].value + this.alarmCountList[2].value;
        });
    },
    //未确认一类告警
    getUnconfirOneCount() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "未确认一类告警", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: 1, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: false, //
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: "0", // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "010000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            if (res.data.status) {
              this.alarmCountList[3].value = 0;
              for (let i = 0; i < res.data.records.length; i++) {
                this.alarmCountList[3].value += res.data.records[i].alarmNum;
              }
            }
          }
        });
    },
    //点击查询按钮
    search() {
      this.loadData();
    },
    //获得告警类型分析数据*
    getAlarmAnalyzeData() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警类型分析数据", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: null,
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: this.timeType, // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "001000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = [];
            if (res.data.records !== null) {
              for (let i = 0; i < this.exceptionCountList.length; i++) {
                for (let j = 0; j < res.data.records.length; j++) {
                  if (
                    res.data.records[j].alarmGroupName ==
                    this.exceptionCountList[i].name
                  ) {
                    data.push({
                      name: res.data.records[j].alarmGroupName,
                      value: res.data.records[j].alarmNum
                    });
                  }
                }
              }
            }
            this.alarmAnalyzeData = data;
          }
        });
    },
    //获得告警信息趋势分析*
    getAlarmInfoTrendsData() {
      let timeCycle = 0;
      switch (this.timeType) {
        case 3:
          timeCycle = 1;
          break;
        case 5:
          timeCycle = 3;
          break;
        case 8:
          timeCycle = 5;
          break;
      }
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警信息趋势分析", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: null,
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: timeCycle, // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "000000100" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            this.alarmInfoTrendsData = res.data.records;
          }
        });
    },
    //获得部门详情列表
    getDepartmentList() {
      this.$ajax
        .get("rest/analysisData/depart", {
          params: {
            title: "查询部门列表", //功能块标题，作为返回值的一部分返回，用于识别具体是哪一个功能块调用该URI
            orgTypes: null, //部门类型，多个用逗号隔开，可为空，为空不作为查询条件
            parentIds: null //上级部门ID，多个用逗号隔开，可为空，为空不作为查询条件
          }
        })
        .then(res => {
          if (res.data.status) {
            this.deptList = res.data.records;
            let data = [];
            this.$each(res.data.records, item => {
              data.push({
                name: item.name,
                orgId: item.id
              });
            });
            let obj;
            var obj = {};
            data.forEach(item => {
              obj[item.orgId] = item.name;
            });
            this.deptColumnData[0].displace = obj;
            this.devColumnData[1].displace = obj;
          }
        });
      this.getDeptAlarmInfoTable();
    },

    //获得部门告警信息统计*
    getDeptAlarmInfoTable() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "部门告警信息统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: null,
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: 0, // String,统计周期（1为小时，3为日，5为月，8为年）this.timeType
            groupBys: "110000000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let data = res.data.records.filter(item => {
              return item.orgId !== null;
            });
            let tempData = [];
            let tempIndex = [];

            this.$each(data, item => {
              let index = tempIndex.indexOf(item.orgId);
              let value = item.alarmNum;
              if (index === -1) {
                tempIndex.push(item.orgId);
                tempData.push({
                  orgId: item.orgId,
                  level_1: 0,
                  level_2: 0,
                  level_3: 0,
                  level_4: 0,
                  alarmCount: 0
                });
                index = tempIndex.length - 1;
              }
              tempData[index]["level_" + item.level] += value;
              tempData[index]["alarmCount"] += value;
            });
            this.deptAlarmInfoTable = tempData;
          }
        });
    },
    //获得设备详细信息
    getDevList() {
      this.$ajax
        .get("rest/analysisData/equip", {
          params: {
            title: "获取设备列表", // String,功能标题
            equipTypes: null, //设备类型，多个用逗号隔开，可为空，为空不作为查询条件
            orgIds: null //部门ID，多个用逗号隔开，可为空，为空不作为查询条件
          }
        })
        .then(res => {
          if (res.data.status) {
            this.devList = res.data.records;
            let data = [];
            this.$each(res.data.records, item => {
              data.push({
                name: item.name,
                orgId: item.id
              });
            });
            let obj;
            var obj = {};
            data.forEach(item => {
              obj[item.orgId] = item.name;
            });
            this.deptColumnData[0].displace = obj;
          }
        });
    },
    //获得设备告警信息统计*
    getDevAlarmInfoTable() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "设备告警信息统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: null,
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: this.timeType, // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "110000010" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let tempData = [];
            let tempIndex = [];

            this.$each(res.data.records, item => {
              if (item.equipId) {
                let index = tempIndex.indexOf(item.orgId + "_" + item.equipId);
                let value = item.alarmNum;
                if (index === -1) {
                  tempIndex.push(item.orgId + "_" + item.equipId);
                  tempData.push({
                    equipName: item.equipName,
                    orgId: item.orgId,
                    level_1: 0,
                    level_2: 0,
                    level_3: 0,
                    level_4: 0,
                    alarmCount: 0
                  });
                  index = tempIndex.length - 1;
                }
                tempData[index]["level_" + item.level] += value;
                tempData[index]["alarmCount"] += value;
              }
            });
            this.devAlarmInfoTable = tempData;
          }
        });
    },
    //获得告警确认情况统计*
    getAlarmConfirmTable() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "告警确认情况统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: "1,2", // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: true,
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: 0, // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "011001000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let tempData = [];
            let tempIndex = [];

            this.$each(res.data.records, item => {
              if (item.alarmGroupName) {
                let index = tempIndex.indexOf(item.alarmGroupId);
                let value = item.alarmNum;
                if (index === -1) {
                  tempIndex.push(item.orgId + "_" + item.equipId);
                  tempData.push({
                    alarmGroupName: item.alarmGroupName,
                    level_1_0: 0,
                    level_1_1: 0,
                    level_2_0: 0,
                    level_2_1: 0,
                    alarmCount: 0
                  });
                  index = tempIndex.length - 1;
                }
                tempData[index][
                  "level_" + item.level + "_" + item.isConfirm
                ] += value;
                tempData[index]["alarmCount"] += value;
                // 计算已确认的占比
                if (tempData[index]["alarmCount"] > 0) {
                  tempData[index]["ratio"] =
                    100 *
                    (tempData[index]["level_1_1"] +
                      tempData[index]["level_2_1"]) /
                    tempData[index]["alarmCount"];
                }
              }
            });
            this.alarmConfirmTable = tempData;
          }
        });
    },
    //获得确认人确认数量统计*
    getConfirmCountTable() {
      this.$ajax
        .get("rest/analysisData/alarmData", {
          params: {
            title: "确认人确认数量统计", // String,功能标题
            userIds: null, // String,用户ID
            alarmLevels: null, // String,告警级别
            alarmGroups: null, // String,告警分组ID
            alarmCodes: this.selectAttentionAlarm, // String,告警项编码
            isConfirm: true,
            receiveTypes: null, // String,用户接收告警类型
            orgIds: null, // String,部门ID
            equipIds: null, // String,设备ID
            productIds: null, // String,产品ID
            alarmItems: null, // String,告警项ID
            startTime: this.beginTime, // String,开始时间
            endTime: this.finishTime, // String,结束时间
            isCount: true, // Boolean,是否统计
            countCycle: 0, // String,统计周期（1为小时，3为日，5为月，8为年）
            groupBys: "011010000" // String,按什么统计或是否显示，111111111（[部门][级别][分类][编码][确认人][是否确认][时间][设备][产品]）
          }
        })
        .then(res => {
          if (res.data.status) {
            let tempData = [];
            let tempIndex = [];

            this.$each(res.data.records, item => {
              if (item.confirmor) {
                let index = tempIndex.indexOf(item.confirmor);
                let value = item.alarmNum;
                if (index === -1) {
                  tempIndex.push(item.orgId + "_" + item.equipId);
                  tempData.push({
                    confirmUser: item.confirmUser,
                    orgId: item.confirmor,
                    alarmCount: 0,
                    alarmCountProportion: 0,
                    level_3: 0,
                    level_3_proportion: 0,
                    level_4: 0,
                    level_4_proportion: 0
                  });
                  index = tempIndex.length - 1;
                }
                // tempData[index]["level_" + item.level] += value;
                // tempData[index]["alarmCount"] += value;
                // tempData[index]["alarmCountProportion"] += value / 10;
                // tempData[index]["level_" + item.level + "_proportion"] =
                //   value / 10;
                if (alarmCountList[1].value > 0) {
                  tempData[index]["alarmCountProportion"] +=
                    value / alarmCountList[1].value * 100;
                  tempData[index]["level_" + item.level + "_proportion"] =
                    value / alarmCountList[1].value * 100;
                }
              }
            });
            this.confirmCountTable = tempData;
            console.log(tempData);
          }
        });
    }
  },
  mounted() {
    this.inintData();
    this.loadData();
  }
};
</script>


